@import url(https://fonts.googleapis.com/css?family=Roboto:500);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* pallete color: https://colorhunt.co/palette/93463 */

.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

body {
  padding-top: 100px;
  background-color: #171313;
}

.color-nav {
  background-color: #72277a;
}

.brand {
  font-family: "Bebas Neue", cursive;
  font-size: 40px;
}

.header-welcome {
  color: white;
  align-self: center;
  font-size: 14px;
}

ul {
  padding: 0;
  list-style: none;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

input[type="number"] {
  margin: 10px;
}

.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 43px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #f5eeee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #ea21a2;
}

.eventNumberLabel {
  font-family: "Bebas Neue", cursive;
  color: white;
}

/* event start */

.EventList {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-gap: 15px;
}

.Event {
  padding: 10px 10px 35px;
  text-align: left;
  border: 1px solid black;
  border-radius: 4px;
  position: relative;
  background-color: #f5eeee;
}

.details-btn {
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  font-family: "Bebas Neue", cursive;
  margin-bottom: 16px;
  background-color: #72277a;
  color: white;
}

.details-btn:hover {
  background-color: #ea21a2;
  color: black;
  grid-column-start: 3;
}

.event-name {
  font-size: 20px;
  font-weight: 600;
}

.event-description {
  overflow-x: hidden;
}

.time-label,
.org-email {
  font-weight: 500;
  color: #72277a;
  color: #ea21a2;
}

.numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}

@media (min-width: 400px) and (max-width: 700px) {
  .EventList {
    grid-template-columns: 1fr 1fr;
  }
}

@media all and (max-width: 450px) {
  .EventList {
    grid-template-columns: 1fr;
  }
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1;
  transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}
@keyframes nprogress-spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

